<template>
    <div class="receiveRecord">
        <TitleBar
            :webview="false"
            title="兑换记录"/>
        <div class="record-table">
            <div
                :key='index'
                v-for='(item,index) in recordList'
                class="record-table-th" >
                <div class="record-table-td">{{ item.date }}</div>
                <div class="record-table-td">{{ item.steps }}步</div>
                <div class="record-table-td coin">+{{ item.coin }}金币</div>
            </div>
        </div>
        <div
            class="shareBtn">
            以上为7天内兑换记录
        </div>
    </div>
</template>

<script>
import { isTakeOverBackByWeb } from '@/utils/jsApiV2/client';
import { addCustomEventListener, removeCustomEventListener } from '@/utils/event';
import { records } from '@/api/running';
import TitleBar from './TitleBar';

export default {
    name: 'ReceiveRecord',
    data() {
        return {
            recordList: [],
        };
    },
    components: {
        TitleBar,
    },
    mounted() {
        this.$root.hidePlaceholder();
        this.getRecords();
        isTakeOverBackByWeb(true);
        addCustomEventListener('backKey', this.backKeyHandler);
    },
    destroyed() {
        isTakeOverBackByWeb(false);
        removeCustomEventListener('backKey', this.backKeyHandler);
    },
    methods: {
        getRecords() {
            records().then((res) => {
                this.recordList = res;
            }).catch(() => {
                this.$error();
            });
        },
        backKeyHandler() {
            this.$router.go(-1);
        },
    },
};
</script>

