<template>
    <div>
        <div class="title-top"/>
        <div class="title-bar">
            <div
                class="back"
                @click="backHandler"
            />
            {{ title }}
            <slot/>
        </div>
    </div>
</template>
<script>
import { quitCurrentWebview } from '@/utils/jsApiV2/client';

export default {
    name: 'TitleBar',
    props: {
        title: {
            type: String,
            default: '',
        },
        webview: {
            type: Boolean,
            default: true,
        },
    },
    methods: {
        backHandler() {
            if (this.webview) {
                quitCurrentWebview();
            } else {
                this.$router.go(-1);
            }
        },
    },
};
</script>
<style scoped>
.title-top {
    height: 25px;
    background: #5ebbff;
}

.back {
    position: absolute;
    line-height: 22px;
    width: 10px;
    height: 10px;
    border-top: 1px solid #fff;
    border-right: 1px solid #fff;
    transform: rotate(-135deg);
    vertical-align: middle;
    margin-left: 20px;
    margin-top: 15px;
}

.title-bar {
    height: 40px;
    font: 17px/40px miui;
    color: #fff;
    text-align: center;
    background: #5ebbff;
    margin-top: -1px;
}

</style>
